import React, { Ref, RefObject, useEffect, useRef } from "react"
import { getAudio } from "../util/audio"
import { SongStore } from "../store"

interface Props {
  poster: string,
  title: string,
  source: string,
  volume: number,
  onTimeChange: Function,
  onDataInitialize: Function,
}

export function PlayerControl(props: Props) {
  useEffect(() => {
    document.title = props.title
    setFavIcon()
  }, [props.title])

  let audio: RefObject<HTMLAudioElement> = useRef()

  useEffect(() => {
    window.audio = audio;
    audio.current.volume = 0.1
    props.onDataInitialize && props.onDataInitialize({})
  }, [])

  useEffect(() => {
    audio.current.src = props.source;
    getAudio().play();
  }, [props.title])

  return <div className={"player-control"}>
    <audio ref={audio} id="audio"
      onTimeUpdate={event => {
        let currentTime = audio.current.currentTime
        props.onTimeChange && props.onTimeChange(currentTime)
      }}
      onEnded={event => {
        SongStore.dispatch({type:'NEXT'})
      }}
    >
      <source
        src={props.source}
        type="audio/x-m4a" />
    </audio>
  </div>
}


function setFavIcon() {
  var link: HTMLLinkElement = document.querySelector("link[rel*='icon']") || document.createElement("link")
  link.type = "image/x-icon"
  link.rel = "icon"
  link.href = "https://luckhunt-blog.oss-cn-beijing.aliyuncs.com/472309f7905298225b97067adcca7bcb0b46d4e6.jfif"
  document.getElementsByTagName("head")[0].appendChild(link)
}
