import { createStore } from 'redux';
import { songs } from '../data/songs';

/**
 * 这是一个 reducer，形式为 (state, action) => state 的纯函数。
 * 描述了 action 如何把 state 转变成下一个 state。
 *
 * state 的形式取决于你，可以是基本类型、数组、对象、
 * 甚至是 Immutable.js 生成的数据结构。惟一的要点是
 * 当 state 变化时需要返回全新的对象，而不是修改传入的参数。
 *
 * 下面例子使用 `switch` 语句和字符串来做判断，但你可以写帮助类(helper)
 * 根据不同的约定（如方法映射）来判断，只要适用你的项目即可。
 */
const initialState = {
    curSong: songs[0],
    curSongIndex: 0,
    songList: songs
}

function SongManager(state = initialState, action) {
    switch (action.type) {
        case 'CHANGE_SONG':
            return Object.assign(state, { curSong: action.song });
        case 'NEXT':
            return getNextSong(state)
        case 'LAST':
            return getLastSong(state)
        default:
            return state;
    }
}

// 创建 Redux store 来存放应用的状态。
// API 是 { subscribe, dispatch, getState }。
export const SongStore = createStore(SongManager);

function getNextSong(state) {
    const index = (state.curSongIndex + 1 + state.songList.length) % state.songList.length;
    return Object.assign(state, {
        curSongIndex: index,
        curSong: songs[index]
    })
}

function getLastSong(state) {
    const index = (state.curSongIndex - 1 + state.songList.length) % state.songList.length;
    return Object.assign(state, {
        curSongIndex: index,
        curSong: songs[index]
    })
}