import "./playerUI.styl"
import React, { Ref, RefObject, useEffect, useRef, useState, StyleHTMLAttributes, CSSProperties, MutableRefObject } from "react"
import { PlayerControl } from "./player"
import { getAudio, isAudioPaused } from "../util/audio"
import { formatTime } from "../util/time"
import { SongStore } from "../store"
import { defaultSong } from "../data/songs"

export function PlayerUI(props) {
  let song = SongStore.getState().curSong || defaultSong;
  let update = useState()[1];
  SongStore.subscribe(() => {
    update(undefined)
  })
  return <div className="player-ui">
    <Songs songs={SongStore.getState().songList} />
    <SongInfo {...song} />
    <Poster {...song} />
    <div className='player-control'>
      <PlayerControl onTimeChange={update} {...song} />
    </div>
    <Progress />
    <Operation />
  </div>
}

function SongInfo({ title, author, album, playing }) {
  return <div className="song-info">
    <div className="title">{title}</div>
    <div className="author">{author}</div>
    <div className="album">{album}</div>
    <div className="playing">{playing}</div>
  </div>
}

function Poster({ poster }) {
  return <img className={"poster"} src={poster} />
}

export function Progress() {
  const percent = getAudio()?.currentTime / getAudio()?.duration * 100;
  const progressBar: MutableRefObject<HTMLDivElement> = useRef(undefined)
  let initialX = useRef(0);
  let dragging = useRef(false);
  let [left, setLeft] = useState(0)
  let [parentWidth, setParentWidth] = useState(0)
  useEffect(() => {
    setParentWidth(progressBar.current.clientWidth)
  }, [])

  useEffect(() => {
    if (!dragging.current) {
      setLeft((percent || 0) / 100 * progressBar.current.clientWidth)
    }
  })

  return <div className="progress">
    <div className="progress-line" ref={progressBar}
      onClick={event => {
        const newPercent = (event.clientX - progressBar.current.offsetLeft) / parentWidth;
        getAudio().currentTime = newPercent * getAudio().duration;
      }}
    >
      <div className="current" style={{ left }} />
      <div className="handle"
        onTouchStart={(event) => {
          dragging.current = true;
          initialX.current = event.touches[0].clientX;
        }}
        onTouchMove={(event) => {
          let offset = event.touches[0].clientX - initialX.current
          setLeft(percent / 100 * parentWidth + offset)
        }} style={{ left }}
        onTouchEnd={event => {
          dragging.current = false;
          const newPercent = left / parentWidth;
          getAudio().currentTime = newPercent * getAudio().duration;
        }}
      >
        <div className="content"></div>
      </div>
      <div className="total"></div>
    </div>
    <div className="time">
      {formatTime(getAudio()?.currentTime)}/{formatTime(getAudio()?.duration)}
    </div>
  </div>
}


export function Songs(props) {
  const list: MutableRefObject<HTMLDivElement> = useRef(undefined);
  useEffect(() => {
    let index = props.songs.findIndex(song => song.title === SongStore.getState().curSong.title);
    list.current.scrollTo({
      top: (list.current.children[index] as HTMLDivElement).offsetTop - 20,
      behavior: "smooth",
    })
  }, [SongStore.getState().curSong.title])
  return <div className="song-list" ref={list}>
    {props.songs.map(song => <SongItem {...song}
      playing={SongStore.getState().curSong.title === song.title}
      onClick={() => {
        SongStore.dispatch({
          type: 'CHANGE_SONG', song
        })
      }} />)}
  </div>
}

export function SongItem({ title, author, album, playing, onClick }) {
  return <div className="song-item" {...{ onClick }}>
    <div className="title">{title}</div>
    <div className="author">{author}</div>
    <div className="album">{album}</div>
    <div className="playing">
      {playing && <Icon icon={'iconplaying'} size={12} />}
    </div>
  </div>
}

function Operation() {
  return <div className="operation">
    <div className="item last" onClick={e => {
      SongStore.dispatch({ type: 'LAST' })
    }}>
      <Icon icon='iconright' size={48} rotate={180} />
    </div>
    <div className="item play" onClick={() => {
      isAudioPaused() ? getAudio().play() : getAudio().pause();
    }}>
      <Icon icon={isAudioPaused() ? 'iconzanting' : 'iconbofang'} size={48} />
    </div>
    <div className="item next" onClick={e => {
      SongStore.dispatch({ type: 'NEXT' })
    }}>
      <Icon icon='iconright' size={48} />
    </div>
  </div>
}

function Icon({ icon, size = 20, rotate = 0 }) {
  const iconStyle: CSSProperties = { fontSize: size + 'px' }
  const divStyle: CSSProperties = {
    height: size,
    width: size,
    display: 'flex',
    alignItems: 'center',
  };
  if (rotate !== 0) {
    divStyle.transform = `rotate(${rotate}deg)`;
  }
  return <div className="component-icon"
    style={divStyle}>
    <i className={"iconfont ".concat(icon)} style={iconStyle} />
  </div>
}